import React, { useRef, useEffect, useCallback, useState} from 'react';
import {isMobile} from 'react-device-detect';
import {connect} from 'react-redux';
import './jolly.scss';
import './heatmap.scss';

const JollyRancherPlayer = ({width, store, spore, progress, preset, setPreset}) => { 
    let ref = useRef();

    useEffect(() => {
        if (ref.current && ref.current.contentWindow) {
            if (spore) {
                ref.current.contentWindow.postMessage({
                    type: "heatmapHash",
                    body: spore.dataHash
                }, "*");
            }
        }
    }, [spore]);

    useEffect(() => {
        if (ref.current && ref.current.contentWindow) {
            ref.current.contentWindow.postMessage({
                type: "sporePosition",
                body: progress
            }, "*");
        }
    }, [progress]);

    useEffect(() => {
        if (ref.current && ref.current.contentWindow) {
            ref.current.contentWindow.postMessage({
                type: "heatmapSize",
                body: [width, 90]
            }, "*");
        }
    }, [width]);
       
    return (
        <iframe
          ref={ref}
          sandbox="allow-scripts allow-same-origin"
          height={110}
          width={width}
          aria-describedby="spore"
          alt="spore.vision"
          src={"https://zequencer.io/ipfs/QmTh6hkgEGGLpVP5XANvDToWMmLNq1VFXcS4vXnCMjrefC/"}
          className="spore-heatmap zora-mediaObject css-hnzuw7 zora-mediaObject--mediaLoaded zora-mediaObject--isFullPage"
          style={{borderWidth: "0px"}}/>
        );
}

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(JollyRancherPlayer);
