import React, { useEffect, useCallback, useState} from 'react';
import {isMobile} from 'react-device-detect';
import {setCurrentPlaylistSong} from 'actions/index.js';
import PulseLoader from 'react-spinners/PulseLoader'
import {connect} from 'react-redux';
import './songplaylist.scss';

const SporePlaylist = ({currentSong, setCurrentSong, sequencerEngine, showPlaylistMenu}) => { 
    let [stems, setStems] = useState(["Twisting Faith", "Psilocybin"]);
    let [loading, setLoading] = useState(false);

    let className = "spores-playlister";

    const loadSong = useCallback((i, name) => {
        console.log("window stemIFRame=", window.stemIFrame);
        if (!window.stemIFrame || !window.stemIFrame.current) {
            return;
        }
        setCurrentSong(name);
        console.log("sending load song=", i);
        window.stemIFrame.current.contentWindow.postMessage({
            type: "load-song",
            body: i
        }, "*");
    }, [setCurrentSong]);

    if (isMobile && !showPlaylistMenu) {
        return '';
    }
    return (
        <div
          onClick={e => e.stopPropagation()}
          className="spores-playlist">
          {isMobile && <div className="artist-name">Keyon Christ</div>}
          {stems.map((x,i) =>
              <div
                onClick={(e) => {
                    console.log("clicked playlist item");
                    if (currentSong === x) {
                        return;
                    }
                    loadSong(i, x);
                }}
                className={x === currentSong ? "song-item current" : "song-item"}>
                  {x}
              </div>)}
        </div>);
};

const mapStateToProps = (state, props) => {
  return {
      showPlaylistMenu: state.playlist.showPlaylistMenu,
      currentSong: state.playlist.currentSong
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
      setCurrentSong: x => dispatch(setCurrentPlaylistSong(x))
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(SporePlaylist);
